.App {
  text-align: center;
  background-color: #282c34;
  padding: 10px;
}

.App-logo {
  height: 40vmin;
}

.Current {
  max-width: 100vw;
  min-height: 65vh;
  align-items: center;
  color: white;
}
.Forecast{
	color: white;
	min-height: 35vh;
}

.time {
	font-size: calc(36vmin);
	font-weight: bold;
	line-height: 1;
}
@media screen and (max-width: 1300px) {
  .time {
	font-size: calc(36vmin - 70px);
	}
}
@media screen and (max-width: 800px) {
  .time {
	font-size: calc(35vmin - 40px);
	}
}
@media screen and (max-width: 600px) {
  .time {
	font-size: calc(30vmin);
	}
}

.date {
	font-size: calc(8vmin);
	font-weight: bold;
}
.temp {
	font-size: calc(15vmin);
	font-weight: bold;
}
.wind {
	font-size: calc(10vmin);
	font-weight: bold;
}
.currentWeather{
	font-size: calc(5vmin);
}
.weatherIcon {
	border-radius: 10px;
	filter: drop-shadow(-3px 10px 5px #bad5ff)
}
.settings {
	width: 5vmin;
	background-color: white;
	border-radius: 1vmin;
}

